.AdjustmentResizeRoot {
    position: fixed;
    z-index: 9999999;
    pointer-events: none;
}

.AdjustmentResizeRootPage {
    position: fixed;
    z-index: 9999999;
    pointer-events: none;
}

.AdjustmentResizeRoot2 {
    position: absolute;
    z-index: 9999999;
    pointer-events: none;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
}
.AdjustmentStretchRoot {
    cursor: pointer ;
    position: absolute;
    z-index: 9999999;
    pointer-events: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    top: 10px;
    right: 10px;
    width: 24px;
    height: 24px;
    background-color: white;
    border-radius: 4px;
    box-shadow: 0 2px 5px rgba(0,0,0,.2);
}
.AdjustmentStretchButton {
    width: 100%;
    height: 100%;
}
.AdjustmentHelpLinesRoot {
    position: fixed;
    z-index: 9999999;
    pointer-events: none;
}
.AdjustmentHelpLinesRect {
    position: absolute;
    pointer-events: none;
    border: 1px dashed #dadada;
}
.AdjustmentHelpSizeRoot {
    position: fixed;
    z-index: 9999999;
    pointer-events: none;
    top: 0;
