.Adjustment-e-pane-page {
    cursor: e-resize;
    content: "";
    box-shadow: 0 2px 2px 2px rgba(0,6,36,.2);
    border-radius: 4px;
    width: 4px;
    height: 50px;
    background-color: #545454;
    position: absolute;
    top: 50%;
    right: -35px;
